define('high-fidelity/adapters/application', ['exports', 'ember-data'], function (exports, DS) {

    'use strict';

    exports['default'] = DS['default'].LSAdapter.extend({
        databaseName: 'hifi',
        version: 2,
        migrations: function migrations() {
            this.addModel('podcast');
            this.addModel('episode');
        }
    });

});