/* jshint ignore:start */

define('high-fidelity/config/environment', ['ember'], function(Ember) {
  var prefix = 'high-fidelity';
/* jshint ignore:start */

try {
  var metaName = prefix + '/config/environment';
  var rawConfig = Ember['default'].$('meta[name="' + metaName + '"]').attr('content');
  var config = JSON.parse(unescape(rawConfig));

  return { 'default': config };
}
catch(err) {
  throw new Error('Could not read config from meta tag with name "' + metaName + '".');
}

/* jshint ignore:end */

});

if (runningTests) {
  require("high-fidelity/tests/test-helper");
} else {
  require("high-fidelity/app")["default"].create({"name":"high-fidelity","version":"3.0.0+aeb3e638"});
}

/* jshint ignore:end */
